




























import { Component, Vue } from "vue-property-decorator";
import DataService from "../services/DataService";

@Component
export default class Settings extends Vue {
  currentSettings: any = {
    beerPrice: 1
  };
  message: string = "";

  async getSettings() {
    this.currentSettings = (await DataService.getSettings()).data;
  }

  updateSettings() {
    if(prompt("Wirklich sicher, das der Bierpreis auf " + this.currentSettings.beerPrice + " € geändert werden soll? Gib zur Bestätigung bitte das Admin-Passwort ein:") === "8848") {
      let data = {
        beerPrice: Number.parseFloat(this.currentSettings.beerPrice)
      }
      DataService.updateSettings(data)
        .then((response) => {
          console.log(response.data);
          this.message = "Status erfolgreich geändert";
        })
        .catch((e) => {
          console.log(e);
        }); 
    }
  }

  mounted() {
    this.message = "";
    this.getSettings();
  }
}
